.toast {
    &__sucess {
        background-color: greenyellow;
    }
}

// default styles from toastify - start
.toastify {
    display: flex;
    align-items: flex-start;
}

.toastify.on {
    width: 80%;
    max-width: 300px;
    font-size: .8rem;
}

.toast-close {
    border-radius: 5px;
    background-color: red;

}

// default styles from toastify - end

.Toastify__toast--error {
    border: 1px solid #EB5757;
    border-radius: 50px !important;
    background: #FAE1E2 !important;
}

.Toastify__toast--error::after {
    // content: url('../assets/images/svg/closeToast.svg'); // Your svg Path
    position: absolute;
    color: #333333;
    font-size: 15px;
    font-weight: 700;
    left: 265px;
    padding-top: 14px !important;
}

.Toastify__toast--error::before {
    // content: url("../assets/images/svg/errorToast.svg"); // Your svg Path
    position: relative;
    z-index: 100000;
    left: 12px;
    top: 6px;
}

.Toastify__toast--success {
    border: 1px solid #3A9EA5 !important;
    border-radius: 5px !important;
    background: #F0F9FA !important;
    color: #333333;
}

.Toastify__toast--success::before {
    // content: url("../../presale_assets/success.svg"); // Your svg Path
    // position: relative;
    z-index: 100000;
    // left: 12px;
    // top: 6px;
}

.Toastify__toast--success::after {
    // content: url('../assets/images/svg/closeToast.svg'); // Your svg Path
    position: absolute;
    color: #333333;
    font-size: 15px;
    font-weight: 700;
    left: 265px;
    padding-top: 14px !important;
}

.Toastify__toast--warning {
    border: 1px solid #E78326 !important;
    border-radius: 50px !important;
    background: #FADFC5 !important;
}

.Toastify__toast--warning::before {
    // content: url("../assets/images/svg/warnToast.svg"); // Your svg Path
    position: relative;
    z-index: 100000;
    left: 12px;
    top: 6px;
}

.Toastify__toast--warning::after {
    // content: url('../assets/images/svg/closeToast.svg'); // Your svg Path
    position: absolute;
    color: #E78326;
    font-size: 15px;
    font-weight: 700;
    left: 265px;
    padding-top: 14px !important;
}

.Toastify__toast-body {
    color: #444C63;
    font-size: 16px;
    padding-left: 20px;
    line-height: 20px;
    padding: 0px;
    padding-top: 25px;
    width: 100%;
    font-weight: 400;
    margin-left: 25px !important;
}

.Toastify__toast>button>svg {
    display: none;
}